html {
  font-family: 'Inter';
  magin: 0px;
  font-size: 16px;
  background: white;
}
body {
  margin: 0px;
}
#root {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin: auto;
  height: 100vh;
}

#mainText {
  max-width: 200px;
  position: absolute;
  top: calc(50vh - 150px);
  left: calc(50vw - 360px);
}
#instructions {
  max-width: 200px;
  position: absolute;
  top: calc(50vh - 300px);
  left: calc(50vw - 360px);
}
#instructions > div {
  font-size: 16px;
}

#buttons > button {
  margin-top: 10px;
}

.buttonPrimary {
  background-color: deeppink;
  border: 0;
  border-radius: .5rem;
  box-sizing: border-box;
  color: white;
  font-size: .875rem;
  font-weight: 600;
  line-height: 1.25rem;
  padding: .25rem 1rem;
  text-align: center;
  text-decoration: none #D1D5DB solid;
  text-decoration-thickness: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.buttonPrimary:hover {
  background-color: #FF2EAD;
}

.buttonPrimary:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.buttonPrimary:focus-visible {
  box-shadow: none;
}

.buttonSecondary {
  background-color: white;
  border: 0;
  border-radius: .5rem;
  box-sizing: border-box;
  color: deeppink;
  font-size: .875rem;
  font-weight: 600;
  line-height: 1.25rem;
  padding: .25rem 1rem;
  text-align: center;
  text-decoration: none #D1D5DB solid;
  text-decoration-thickness: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.buttonSecondary:hover {
  background-color: #EEE;
}

.buttonSecondary:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.buttonSecondary:focus-visible {
  box-shadow: none;
}

#googlyEyes {
  width: 300px;
  height: 300px;
  background: lightgrey;
}
#googly1 {
  z-index: 1000;
  position: absolute;
  top: calc(50vh - 60px);
  left: calc(50vw - 70px);
  border: 5px solid black;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: white;
}
#googly2 {
  z-index: 1000;
  position: absolute;
  top: calc(50vh - 60px);
  left: calc(50vw + 10px);
  border: 5px solid black;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: white;
}
#pupil1 {
  position: relative;
  top: 8px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: black;
}
#pupil2 {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: black;
}
#subTitle {
  margin: 10px 0px 10px 0px;
  line-height: 20px;
  font-size: 16px;
}
#subTitle > div {
  line-height: 20px;
}
.recording {
  font-size: 16px;
}
footer {
  position: absolute;
  bottom: 10px;
}
#title {
  font-size: 33px;
  line-height: 29px;
  font-weight: 900;
}
#badBrowser {
  color: crimson;
  font-size: 12px;
}
a {
  color: deeppink;
  text-decoration: none;
}
.playerContainer {
  position: absolute;
  top: calc(50vh - 150px);
  left: calc(50vw - 150px);
}

#loadingContainer {
  position: absolute;
  top: calc(50vh - 150px);
  left: calc(50vw - 150px);
  width: 300px;
  height: 300px;
  background: lightgrey;
}

#loadingContainer > span {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  height: 100%;
}

.recordedContainer {
  position: absolute;
  top: calc(50vh - 150px);
  left: calc(50vw - 150px);
  height: 300px;
  transform: rotateY(180deg);
  -webkit-transform:rotateY(180deg); /* Safari and Chrome */
  -moz-transform:rotateY(180deg); /* Firefox */
}
.playerItem {
  position: absolute;
}
.currentVideo {
  z-index: 1000;
}
.playerItemHidden {
  position: absolute;
  visibility: hidden;
}

@media only screen and (max-width: 740px) {
    html {
        font-size: 14px;
    }
    #mainText {
        max-width: 300px;
        position: absolute;
        top: 10px;
        left: calc(50vw - 150px);
    }
    #title {
        font-size: 35px;
        line-height: 29px;
    }
    #subTitle {
        margin: 10px 0px 10px 0px;
        font-size: 16px;
        line-height: 18px;
    }
    #subTitle > div {
        line-height: 16px;
    }
    #subTitleInstructions {
        margin: 5px 0px 5px 0px;
        line-height: 15px;
    }
    #instructions {
        max-width: 200px;
        position: absolute;
        top: calc(50vh - 300px);
        left: calc(50vw - 360px);
    }
    #buttons {
        position: absolute;
        top: calc(50vh + 160px);
    }
    #buttons > button {
        margin-right: 5px;
    }
    #googly1 {
        z-index: 1000;
        position: absolute;
        top: calc(50vh - 60px);
        left: calc(50vw - 70px);
        border: 5px solid black;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background: white;
    }
    #googly2 {
        z-index: 1000;
        position: absolute;
        top: calc(50vh - 60px);
        left: calc(50vw + 10px);
        border: 5px solid black;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background: white;
    }
    .playerContainer {
        z-index: 1;
        position: absolute;
        top: calc(50vh - 150px);
        left: calc(50vw - 150px);
    }
    .recordedContainer {
        position: absolute;
        top: calc(50vh - 150px);
        left: calc(50vw - 150px);
        height: 300px;
        transform: rotateY(180deg);
        -webkit-transform:rotateY(180deg); /* Safari and Chrome */
        -moz-transform:rotateY(180deg); /* Firefox */
    }
}
